<template>
	<view>
		<cu-custom bgColor="bg-gradual-green" :isBack="true">
			<view slot="backText">返回</view>
			<view slot="content">搜索</view>
		</cu-custom>
		<view class="cu-bar search bg-white">
			<view class="search-form round">
				<text class="cuIcon-search"></text>
				<input type="text" placeholder="搜索图片、文章、视频" confirm-type="search" :value="value" @input="onInput" @focus="onFocus"></input>
			</view>
			<view class="action">
				<button class="cu-btn bg-green shadow-blur round" @click="onHandleSearch">搜索</button>
			</view>
		</view>
		<view class="result">
			<view class="bg-white padding-lr">
				<!-- 搜索条件 -->
				<view v-if="flag">
					<view class="flex flex-bettwen-space flex-align-center padding-tb-sm">
						<text>历史搜索</text>
						<text class="cuIcon-delete lg text-gray" @click="onClear"></text>
					</view>
					<view class="padding-tb-sm">
						<text
							class="cu-tag radius padding-sm margin-bottom-sm"
							v-for="(item, index) in historyList"
							:key="index"
							:data-item="item"
							:data-index="index"
							@click="selectTag"
						>{{ item }}</text>
					</view>
				</view>
				<!-- 搜索结果 -->
				<view class="list" v-else>
					<view>
						<scroll-view scroll-x class="nav text-center">
						  <view class="cu-item" :class="[{'text-bold cur': index == tabCur}]" v-for="(item, index) in menuList" :key="index" @click="tabSelect" :data-id="index" :data-item="item">
						    {{ item.label }}
						  </view>
						</scroll-view>
					</view>
					<view v-if="tabCur == 2">
						<ActiveCard v-for="(item, index) in list" :key="index"></ActiveCard>
					</view>
					<view v-else>
						<Card v-for="(item, index) in list" :key="index" :item="item" :source="1" :status="1"></Card>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import Card from '@/components/card';
	import ActiveCard from '@/components/activeCard';
	export default {
		components: {
			Card,
			ActiveCard
		},
		data() {
			return {
				flag: true, // 是否显示搜索历史
				value: '',
				historyList: [], // 历史记录
				list: [] ,// 搜索记录
				tabCur: 0,
				scrollLeft:0,
				menuList: [{
					index: 1,
					label: '广场'
				}, {
					index: 2,
					label: '官方'
				}, {
					index: 3,
					label: '活动'
				}]
			}
		},
		created() {
			this.historyList = uni.getStorageSync('searchHistory') || [];
		},
		methods: {
			selectTag(e) {
				const item = e.currentTarget.dataset.item;
				const index = e.currentTarget.dataset.index;
				this.historyList.splice(index, 1);
				this.value = item;
				this.onHandleSearch();
			},
			onInput(e) {
				this.value = e.detail.value;
			},
			onFocus(e) {
				this.flag = true;
			},
			tabSelect(e) {
				const data = e.currentTarget.dataset;
				this.tabCur = data.id;
				this.scrollLeft = (data.id-1) * 60;
			},
			onHandleSearch() {
				if (!this.value) return;
				if (!this.historyList.includes(this.value)) {
					this.historyList.unshift(this.value);
					uni.setStorageSync('searchHistory', this.historyList)
				}
				// 这里写接口,传this.value的值
				this.search();
				// 隐藏搜索历史
				this.flag = false;
			},
			search() {
				if (this.tabCur == 2) {
					new Array(10).fill(0).map((v, i) => this.list.push({
						id: i,
						title: '帖子' + i,
						info: '我是帖子介绍，介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍',
						address: '望京soho' + i,
						price: 300 + i
					}));
				} else {
					new Array(10).fill(0).map((v, i) => this.list.push({
						id: i,
						title: '帖子' + i,
						info: '我是帖子介绍，介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍',
						address: '望京soho' + i,
						price: 300 + i
					}));
				}
			},
			onClear() {
				this.historyList = [];
				uni.setStorageSync('searchHistory', '');
			}
		}
	}
</script>

<style lang="scss" scoped>
</style>
